@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100&family=Josefin+Sans&family=Jost&family=Source+Sans+Pro&display=swap");
/* font-family: 'Inter', sans-serif;
font-family: 'Josefin Sans', sans-serif;
font-family: 'Jost', sans-serif;
font-family: 'Source Sans Pro', sans-serif; */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Jost", sans-serif;
}

html {
  font-size: 100%;
  height: 100%;
  background: #eee;
}

body {
  background-color: #eeeed4;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  font-family: "Jost", sans-serif;
  width: 100vw;
  height: 100vh;
  margin: auto;
}

.box {
  width: 25vw;
  height: 60vh;
  border-radius: 0.5rem !important;
  box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
  background-color: #92a8c9;
  position: relative;
  overflow: hidden;
  min-width: 20rem;
  min-height: 33rem;
  transform: translate3d(0, 0, 0);
}

.inputdata {
  margin: 20px;
}

.inputdata input {
  width: 70%;
  padding: 10px;
  margin-left: 3rem;
  outline: none;
  border: none;
  font-size: 20px;
  border-radius: 10px;
  margin-bottom: 30px;
  caret-color: red;
}
.location {
  height: 55%;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3em;
}
.location .fa-street-view {
  font-size: 60px;
  margin-right: 30px;
  animation: fas-anime 3s linear infinite alternate;
}

@keyframes fas-anime {
  from {
    transform: scale(1.1);
    transform: translateX(-0.5rem);
  }
  to {
    transform: scale(1.5);
    transform: translateX(0.5rem);
  }
}

.info {
  margin-top: 20%;
}
.info .temp {
  font-size: 3rem;
  margin: 20px 0 20px 0;
  text-align: center;
}

.info .tempmin-max {
  font-size: 1.2rem;
  font-weight: 600;
  text-align: center;
  color: red;
  margin-top: 0.4rem;
}

.date{
  font-size: 16px;
  text-align: center;
  margin: 12px auto;
  font-weight: 400;
}

.error{
  font-size: 3rem;
  text-align: center;
  margin-top: 4rem;
  font-weight: 400;
  color: red;
  text-shadow: 1px 2px 3px whitesmoke;
}

.info .wave {
  opacity: 0.3;
  position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -20rem;
  transform-origin: 50% 48%;
  border-radius: 44%;
  animation: drift 3000ms infinite linear;
  z-index: 1;
}

.-two {
  animation: drift 7000ms infinite linear;
  opacity: 0.1;
  z-index: 3 !important;
}

.-three {
  animation: drift 5000ms infinite linear;
  opacity: 0.2;
  z-index: 2 !important;
}

.box::after {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  top: 0;
  left: 0;
  transform: translate3d(0, 0, 0);
  z-index: 11;
}

@keyframes drift {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* responsive code start here  */

@media (max-width: 600px) {
  .box {
    width: 90vw;
    height: 80vh;
  }
  .wave {
    top: 85%;
  }
  .location {
    font-size: 3.4em;
  }

  .info .temp {
    font-size: 24px;
  }

  .info .tempmin-max {
    font-size: 16px;
  }
}
